import { reactive, ref, toRefs, watch } from 'vue';
import { useTranslate } from '@demo/use-translate';
const i18n = {
    'zh-CN': {
        info: '密码为 6 位数字',
        showInfo: '提示信息',
        addGutter: '格子间距',
        errorInfo: '密码错误',
        removeMask: '明文展示',
        customLength: '自定义长度',
    },
    'en-US': {
        info: 'Some tips',
        showInfo: 'Show Info',
        addGutter: 'Add Gutter',
        errorInfo: 'Password Mistake',
        removeMask: 'Remove Mask',
        customLength: 'Custom Length',
    },
};
export default {
    setup() {
        const t = useTranslate(i18n);
        const initialValue = {
            showInfo: '123',
            addGutter: '123',
            basicUsage: '123',
            removeMask: '123',
            customLength: '123',
        };
        const refMap = {
            showInfo: ref(),
            addGutter: ref(),
            basicUsage: ref(),
            removeMask: ref(),
            customLength: ref(),
        };
        const state = reactive({
            value: initialValue,
            current: 'basicUsage',
            errorInfo: '',
        });
        const onInput = (key) => {
            const { value, current } = state;
            const maxlegnth = current === 'customLength' ? 4 : 6;
            const newValue = (value[current] + key).slice(0, maxlegnth);
            value[current] = newValue;
            if (current === 'showInfo' &&
                newValue.length === 6 &&
                newValue !== '123456') {
                state.errorInfo = t('errorInfo');
            }
        };
        const onDelete = () => {
            const { value, current } = state;
            value[current] = value[current].slice(0, value[current].length - 1);
            if (current === 'showInfo') {
                state.errorInfo = '';
            }
        };
        watch(() => state.current, (value) => {
            if (value) {
                const vm = refMap[value].value;
                if (vm) {
                    const { top } = vm.$el.getBoundingClientRect();
                    window.scrollTo(0, window.pageYOffset + top);
                }
            }
        });
        return {
            ...toRefs(state),
            ...refMap,
            t,
            onInput,
            onDelete,
        };
    },
};
